
#frameContainer {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-overflow-scrolling: touch;
  overflow: scroll;
  background-color: #eee;
}

#nuomiFrame {
  width: 1px;
  height: 1px;
  min-width: 100%;
  min-height: 100%;
  max-width: 100%;
  max-height: 100%;
}

#returnButton {
  position: absolute;
  left: 0px;
  bottom: 100px;
  width: 100px;
  height: 25px;
  cursor: pointer;
  color: #fff;
  background-color: #3385ff;
}



